.body-container {
  color: #2d2e30;
  width: 1280px;
  background: #fff;
  margin: 0 auto;
  font-family: Helvetica Neue, Roboto, Verdana, Tahoma, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

.nav-container {
  height: 216px;
  background-image: url("../header-background.28cae044.jpg");
  justify-content: center;
  align-items: center;
  display: flex;
}

.main-container {
  background: #fff;
  margin-top: 20px;
}

.input-class {
  font-size: 40px;
}

.cards-list {
  flex-wrap: wrap;
  gap: 32px 16px;
  margin: 60px 30px;
  display: flex;
}

.card {
  width: 395px;
  height: 634px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.card-img {
  width: 395px;
  height: 574px;
  background: url("../film500.80a0ab88.jpg");
  border-radius: 5px;
}

.card-text {
  font-family: var(--font-family);
  text-transform: uppercase;
  color: #000;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

@media screen and (min-width: 768px) and (max-width: 1157px) {
  .body-container {
    width: 768px;
  }

  .cards-list {
    flex-wrap: wrap;
    gap: 32px;
    margin: 60px 32px;
    display: flex;
  }

  .card {
    width: 336px;
    height: 494px;
  }

  .card-img {
    width: 336px;
    height: 455px;
  }

  .card-text {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 1.33333;
  }
}

@media screen and (max-width: 767px) {
  .body-container {
    width: 320px;
  }

  .cards-list {
    flex-wrap: wrap;
    gap: 32px;
    margin: 20px 20px 40px;
    display: flex;
  }

  .card {
    width: 280px;
    height: 443px;
  }

  .card-img {
    width: 280px;
    height: 402px;
  }

  .card-text {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 1.33333;
  }
}

.control-pagination {
  margin: 60px, auto;
  justify-content: center;
  display: flex;
}

.control-pagination ul {
  width: 374px;
  height: 40px;
  flex-wrap: nowrap;
  gap: 3px;
  display: flex;
}

.control-pagination ul li {
  color: #000;
  width: 40px;
  height: 40px;
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  list-style: none;
  display: flex;
}

.control-pagination ul li.numb {
  border-radius: 5px;
}

.control-pagination ul li.numb.first, .control-pagination ul li.numb.last {
  align-items: center;
}

.control-pagination ul li.dots {
  cursor: default;
  width: auto;
  padding-bottom: 8px;
}

.control-pagination ul li.btn {
  background: #f7f7f7;
  border-radius: 5px;
}

.control-pagination ul li.prev {
  transform: rotate(-180deg);
}

.control-pagination li.active, .control-pagination ul li.numb:hover {
  color: #fff;
  background: rgba(255, 107, 8, .5);
}

.control-pagination ul li:first-child:hover, .control-pagination ul li:last-child:hover {
  background: rgba(247, 247, 247, .8);
}

.footer-container {
  background: #e0cece;
}

